

<template>
  <div class="layout">
    <Layout>
      <Header>
        <Menu mode="horizontal" theme="dark" active-name="1" @on-select="toMoudle" >
          <div class="layout-logo layoutLogo">LOGO</div>
          <div class="layout-nav menuItem" >
            <MenuItem :name="item.name_en" v-for="item in headerList"  :class="{menuAvtive:avtiveMenu==item.name_en}">
              <Icon :type="item.icontype"></Icon>
              {{item.name_ch}}
            </MenuItem>
          </div>
          <div class="layout-user"><div class="layout-user-img"></div><span style="float:right;color:#fff">name<Icon type="arrow-down-b" style="color:#fff;margin: 4px 0 0 7px;"></Icon></span></div>
        </Menu>
      </Header>
      <Layout :style="{padding: '0px'}">
        <router-view></router-view>
      </Layout>
      <!--<Footer class="layout-footer-center" style="background: #fff">2011-2016 &copy; TalkingData</Footer>-->
    </Layout>
  </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        avtiveMenu:'home',
        headerList:[
          {
            name_en:'home',
            name_ch:'首页',
            index:'1',
            path:'/home',
            icontype:'home'
          },
          {
            name_en:'projectManage',
            name_ch:'项目管理',
            index:'2',
            path:'/projectManage',
            icontype:'settings'
          },
          {
            name_en:'laboratoryManage',
            name_ch:'实验中心管理',
            index:'3',
            path:'/laboratoryManage',
            icontype:'medkit'
          },
          {
            name_en:'userManage',
            name_ch:'用户管理',
            index:'4',
            path:'/userManage',
            icontype:'person'
          },
          {
            name_en:'settingManage',
            name_ch:'设置管理',
            index:'5',
            path:'/settingManage',
            icontype:'ios-gear'
          },
          {
            name_en:'reportManage',
            name_ch:'报表管理',
            index:'6',
            path:'/reportManage',
            icontype:'arrow-graph-up-right'
          },
          {
            name_en:'reportRightsManage',
            name_ch:'报表权限管理',
            index:'7',
            path:'/reportRightsManage',
            icontype:'key'
          },
        ]
      };
    },
    computed: {
      menuitemClasses: function () {
        return [
          'menu-item',
          this.isCollapsed ? 'collapsed-menu' : ''
        ]
      }
    },
    methods:{
      //模块间跳转
      toMoudle(name){
        localStorage.setItem('moudlename',name)
        this.avtiveMenu=name
        this.$router.push({
          name: name,
          params: {}
        })
      },
    },
    mounted(){
      //登录默认定到home，如在其他模块强刷则定位到该模块
      if(localStorage.getItem('moudlename')){
        this.toMoudle(localStorage.getItem('moudlename'))
      }else{
        this.toMoudle('home')
      }
    }
  }
</script>
<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
  .ivu-menu-dark{
    background: #1D8FBD!important;
  }
  .ivu-layout-header{
    background: #1D8FBD!important;
  }
  .layout-logo{
    text-align: center;
    line-height: 42px;
    width: 108px;
    height: 42px;
    /*background: url("../../image/logo2.png") no-repeat;*/
    background: #fff;
    background-size:108px 42px;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 10px;
    left: 20px;
  }
  .layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }
  .layout-footer-center{
    text-align: center;
  }
  .collapsed-menu span{
    width: 0px;
    transition: width .2s ease;
  }
  .collapsed-menu i{
    transform: translateX(5px);
    transition: font-size .2s ease .2s, transform .2s ease .2s;
    vertical-align: middle;
    font-size: 22px;
  }
  .menuAvtive{
    color:#fff!important;
    /*background: #246580;*/
    background: #1c86b1;
    height:64px;

  }
  .menuItem{
    width:auto
  }
  .layoutLogo{
    left:-20px;
  }
  .layout-user{
    float:right;
    width:100px;
  }
  .layout-user-img{
    height:40px;
    width:40px;
    background: #fff;
    border-radius: 5px;
    margin-top: 12px;
    float:left;
  }
</style>



